import React from "react";

function FinanceSummary({ expenses, incomes }) {
  const totalIncome = incomes.reduce((sum, i) => sum + i.amount, 0);
  const totalExpenses = expenses.reduce((sum, e) => sum + e.amount, 0);
  const balance = totalIncome - totalExpenses;

  return (
    <>
      <div className="finance-card" style={{background: "#10b981", color:"#fff"}}>
        <h3>Total Income</h3>
        <p>${totalIncome.toFixed(2)}</p>
      </div>
      <div className="finance-card" style={{background: "#ef4444", color:"#fff"}}>
        <h3>Total Expenses</h3>
        <p>${totalExpenses.toFixed(2)}</p>
      </div>
      <div className="finance-card" style={{background: "#4f46e5", color:"#fff"}}>
        <h3>Balance</h3>
        <p>${balance.toFixed(2)}</p>
      </div>
    </>
  );
}

export default FinanceSummary;
