import React, { useState } from "react";

function ExpenseTable({ expenses, updateExpense, deleteExpense }) {
  const [editingId, setEditingId] = useState(null);
  const [editData, setEditData] = useState({ description:"", amount:"", category:"" });

  const handleEdit = (expense) => {
    setEditingId(expense.id);
    setEditData(expense);
  };

  const handleSave = () => {
    updateExpense(editData);
    setEditingId(null);
  };

  return (
    <table className="expense-table">
      <thead>
        <tr>
          <th>Description</th>
          <th>Category</th>
          <th>Amount</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {expenses.map(e => (
          <tr key={e.id}>
            <td>
              {editingId === e.id ? <input value={editData.description} onChange={ev=>setEditData({...editData, description:ev.target.value})}/> : e.description}
            </td>
            <td>
              {editingId === e.id ? <input value={editData.category} onChange={ev=>setEditData({...editData, category:ev.target.value})}/> : e.category}
            </td>
            <td>
              {editingId === e.id ? <input type="number" value={editData.amount} onChange={ev=>setEditData({...editData, amount:parseFloat(ev.target.value)})}/> : `$${e.amount.toFixed(2)}`}
            </td>
            <td>
              {editingId === e.id ? <button onClick={handleSave}>Save</button> :
                <>
                  <button onClick={()=>handleEdit(e)}>Edit</button>
                  <button onClick={()=>deleteExpense(e.id)}>Delete</button>
                </>
              }
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}

export default ExpenseTable;
