import React, { useState, useEffect } from "react";
import AddExpense from "./components/AddExpense";
import AddIncome from "./components/AddIncome";
import FinanceSummary from "./components/FinanceSummary";
import ExpenseChart from "./components/ExpenseChart";
import ExpenseTable from "./components/ExpenseTable";
import AdviceBox from "./components/AdviceBox";
import "./App.css";

function App() {
  const [expenses, setExpenses] = useState(() =>
    JSON.parse(localStorage.getItem("expenses") || "[]")
  );
  const [incomes, setIncomes] = useState(() =>
    JSON.parse(localStorage.getItem("incomes") || "[]")
  );

  const [currentMonth, setCurrentMonth] = useState(new Date().getMonth());
  const [currentYear, setCurrentYear] = useState(new Date().getFullYear());

  useEffect(() => { localStorage.setItem("expenses", JSON.stringify(expenses)); }, [expenses]);
  useEffect(() => { localStorage.setItem("incomes", JSON.stringify(incomes)); }, [incomes]);

  const addExpense = (expense) => setExpenses([expense, ...expenses]);
  const updateExpense = (updated) => setExpenses(expenses.map(e => e.id === updated.id ? updated : e));
  const deleteExpense = (id) => setExpenses(expenses.filter(e => e.id !== id));

  const addIncome = (income) => setIncomes([income, ...incomes]);
  const updateIncome = (updated) => setIncomes(incomes.map(i => i.id === updated.id ? updated : i));
  const deleteIncome = (id) => setIncomes(incomes.filter(i => i.id !== id));

  const prevMonth = () => {
    if(currentMonth === 0){
      setCurrentMonth(11);
      setCurrentYear(currentYear - 1);
    } else setCurrentMonth(currentMonth - 1);
  };

  const nextMonth = () => {
    if(currentMonth === 11){
      setCurrentMonth(0);
      setCurrentYear(currentYear + 1);
    } else setCurrentMonth(currentMonth + 1);
  };

  const monthlyExpenses = expenses.filter(e => e.month === currentMonth && e.year === currentYear);
  const monthlyIncomes = incomes.filter(i => i.month === currentMonth && i.year === currentYear);

  return (
    <div className="app-container">
      <header className="header">
        <h1>Monthly Finance Manager</h1>
        <div className="month-navigation">
          <button onClick={prevMonth}></button>
          <h2>{new Date(currentYear, currentMonth).toLocaleString('default', { month: 'long', year: 'numeric' })}</h2>
          <button onClick={nextMonth}></button>
        </div>
      </header>

      <main className="main-content">
        <div className="top-cards">
          <FinanceSummary expenses={monthlyExpenses} incomes={monthlyIncomes}/>
        </div>

        <div className="forms-container">
          <AddIncome addIncome={addIncome} currentMonth={currentMonth} currentYear={currentYear}/>
          <AddExpense addExpense={addExpense} currentMonth={currentMonth} currentYear={currentYear}/>
        </div>

        <div className="charts-tables">
          <ExpenseChart expenses={monthlyExpenses} incomes={monthlyIncomes}/>
          <ExpenseTable
            expenses={monthlyExpenses}
            updateExpense={updateExpense}
            deleteExpense={deleteExpense}
            incomes={monthlyIncomes}
            updateIncome={updateIncome}
            deleteIncome={deleteIncome}
          />
        </div>

        
      </main>
    </div>
  );
}

export default App;
