import React from "react";
import { Pie } from "react-chartjs-2";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";

ChartJS.register(ArcElement, Tooltip, Legend);

function ExpenseChart({ expenses, incomes }) {
  if (!incomes.length) return null;

  const totalIncome = incomes.reduce((sum, i) => sum + i.amount, 0);
  const totalExpenses = expenses.reduce((sum, e) => sum + e.amount, 0);

  // Unique categories
  const categories = [...new Set(expenses.map(e => e.category))];

  // Assign colors
  const categoryColors = [
    "#4f46e5", "#f59e0b", "#10b981", "#ef4444",
    "#8b5cf6", "#f43f5e", "#3b82f6", "#14b8a6"
  ];

  // Data values for pie: each category as proportion of income
  const dataValues = categories.map(cat => {
    const sum = expenses
      .filter(e => e.category === cat)
      .reduce((total, e) => total + e.amount, 0);
    return sum > totalIncome ? totalIncome : sum;
  });

  // Add remaining income as a slice
  const remaining = totalIncome - totalExpenses > 0 ? totalIncome - totalExpenses : 0;
  const data = {
    labels: [...categories, "Remaining"],
    datasets: [
      {
        label: "Expenses vs Income",
        data: [...dataValues, remaining],
        backgroundColor: [...categories.map((_, i) => categoryColors[i % categoryColors.length]), "#cbd5e0"],
      },
    ],
  };

  const options = {
    plugins: {
      tooltip: {
        callbacks: {
          label: function (tooltipItem) {
            const label = tooltipItem.label;
            if (label === "Remaining") return `Remaining: $${remaining}`;
            const expenseSum = expenses
              .filter(e => e.category === label)
              .reduce((total, e) => total + e.amount, 0);
            return `${label}: $${expenseSum} / $${totalIncome} income`;
          },
        },
      },
    },
  };

  return (
    <div className="chart-container">
      <Pie data={data} options={options} />
    </div>
  );
}

export default ExpenseChart;
